import React from 'react';
import { Link } from 'react-router-dom';
import Studying from './Images/course.png';
import Books from './Images/library.png';
import OnlineCourse from './Images/online.png';
import './personalprogress.css';
import getNavBar from './getNavBar';

export default function PhotoGalleryIOW() {
  return (
    <div className="professionaldevmaindiv">
      <div>{
        getNavBar()
      }</div>
      <h1 className>Professional Development</h1>
      <div className="imgcontainer">
        <img className="pdpic" src={Studying} alt="studying picture" />
        <img className="pdpic" src={Books} alt="books picture" />
        <img className="pdpic" src={OnlineCourse} alt="online picture" />
      </div>
      <br />

      <Link to="/" className="text">Home</Link>
    </div>
  );
}